:root {
  --blue-color: #00acdc;
  --text-main-color: #060b35;
  --text-secondary-color: #616070;
  --text-title-color-hover: #34568b;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars .stars__star:last-child {
  margin-right: 0;
}

.stars--1 .stars__star:first-child, .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.page {
  grid-template-rows: 60px 50px auto 72px;
  height: 100vh;
  margin: 0;
  display: grid;
}

.header {
  box-sizing: border-box;
  grid-row: 1 / 2;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 1px 2px 4px 1px #0000000d;
}

.nav__list {
  box-sizing: border-box;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style-type: none;
  display: flex;
}

.nav__item {
  box-sizing: border-box;
  margin-right: 20px;
  display: inline-block;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  text-transform: uppercase;
  color: var(--text-main-color);
  box-sizing: border-box;
  align-items: center;
  height: 60px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition-duration: .3s;
  display: flex;
  position: relative;
}

.nav__link:hover, .is-active {
  color: var(--blue-color);
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  text-align: center;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__link[data-qa="nav-hover"]:hover {
  color: var(--blue-color);
}

.products {
  box-sizing: border-box;
  grid-row: 2 / 4;
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding-top: 50px;
  padding-bottom: 72px;
  display: grid;
}

@media (width <= 1023px) {
  .products {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width <= 767px) {
  .products {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width <= 487px) {
  .products {
    grid-template-columns: repeat(1, 200px);
  }
}

.product_card[data-qa="card"] {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition-duration: .3s;
  display: block;
}

img {
  overflow-clip-margin: content-box;
  overflow: clip;
}

.product_photo {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.product_name {
  color: var(--text-main-color);
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  display: block;
}

.product_card:hover {
  transform: scale(1.2);
}

.product_card:hover .product_name {
  color: var(--text-title-color-hover);
}

.product_code {
  color: var(--text-secondary-color);
  margin: 0 0 16px;
  font-size: 10px;
  line-height: 14px;
  display: block;
}

.note {
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 24px;
  display: flex;
}

.reviews {
  text-align: right;
  color: var(--text-main-color);
  font-size: 10px;
  line-height: 14px;
  display: block;
}

.price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.price_name {
  color: var(--text-secondary-color);
  font-size: 12px;
  line-height: 18px;
  display: block;
}

.price_number {
  color: var(--text-main-color);
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 18px;
  display: block;
}

.button_buy {
  text-align: center;
  box-sizing: border-box;
  border: 1px solid var(--blue-color);
  background-color: var(--blue-color);
  color: #fff;
  text-transform: uppercase;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition-duration: .3s;
  display: flex;
}

.button_buy:hover {
  color: var(--blue-color);
  background-color: #fff;
}

